<template>
  <el-container>
    <el-header style="height: auto; padding: 0px">
      <layout></layout>
    </el-header>
    <el-main>
     <el-row>
       <el-col :span="18" :offset="3" v-if="post">
           <h2 class="text-center"> {{post.title}} </h2>
           <el-row >
               <el-col :span="11" >

                <el-avatar :size="50" src="https://empty" style="float:right"  @error="errorHandler">
                    <img src="../../../public/pp-img2.jpg"/>
                </el-avatar>
                <!--<el-avatar :size="48" src="https://empty"  @error="errorHandler">
                    <img src="../../../public/pp-img1.jpeg"/>
                </el-avatar>-->
               </el-col>
                <el-col :span="13" style="margin-top: auto; margin-bottom: auto;float:left">
                     <b>Gülşah Alıcı</b> <small>· {{ timeConvert(post.created_at) }}</small>
                </el-col>
            </el-row>
          
           <div v-html="post.content"></div>
       </el-col>
     </el-row>
    </el-main>
    <el-footer class="text-center" style="background-color: #a66081;height: auto; padding: 5px">
      <small style="color: white"><b>©2022 Gülşah Alıcı</b></small>
    </el-footer>
  </el-container>
  <div>
  </div>
</template>

<script>
import Layout from '../Layout.vue'
import { useRouter } from 'vue-router'
import { useLoadPost } from '@/firebase'
import { ref } from 'vue'

export default {
  components: { Layout },
  setup() {
    const router = useRouter()

    const slug = router.currentRoute.value.params.slug

    var post = ref(null)
    useLoadPost(slug).then((data) =>
    {
        console.log("hey",data)
        post.value = data
    })
    

    const timeConvert = (timestamp) => {
      var d = new Date(timestamp.seconds * 1000)
      var curr_date = d.getDate()
      var curr_month = d.getMonth() + 1
      var curr_year = d.getFullYear()

      return curr_date + "-" + curr_month + "-" + curr_year
    }

    return {
        post,
        timeConvert
    }
  }
}
</script>

